.quickview {
  display: flex;
  flex-direction: column;
  background-color: #fafafa;
  max-width: calc(100% - 50px);
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 35;
  transform: translateZ(0);
  transition: 0.3s ease;
  backface-visibility: hidden;
  perspective: 1000;
  will-change: transform;
}
.quickview.is-marginless {
  max-width: 100%;
}
