.short-input {
  max-width: 7em;
  margin: 0 1em;
}

.plotSlot {
  width: 100%;
}

option {
  color: black;
}

hr {
  width: 90%;
  margin: 1em 0;
}

.report {
  display: block;
  margin-bottom: 1em;
}

table th:not([align]) {
  text-align: center;
  padding: 1em;
}

table td:not([align]) {
  text-align: center;
  padding: 1em;
}

.averagedTable td {
  color: #fafafa;
}

.tableContainer {
  border: 1px solid #634e96;
  width: 100%;
  padding-right: 1em;
}

.tableContainer thead {
  background-color: #e1e1e1;
}

.align {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 1em;
  width: 100%;
}

.v-center {
  align-items: center;
}

.h-center {
  justify-content: center;
}

.h-end {
  justify-content: flex-end;
}

.gap {
  margin-right: 2em;
}

.cl {
  color: black !important;
}
.cl .cl-content {
  color: black !important;
  padding: 1em 2em !important;
}
.cl .cl-content * {
  color: black !important;
}
.cl .cl-actionbar {
  background-color: #634e96 !important;
}
.cl .cl-actionbar .cl-button svg {
  stroke: #fafafa;
  fill: #fafafa;
}
.cl .cl-actionbar .cl-button:hover {
  background-color: #836ac05c !important;
}
.cl .cl-actionbar .cl-button .active {
  background-color: #4a4a4ae6 !important;
}
.cl textarea {
  color: black !important;
}

.interact {
  touch-action: none;
}

.material-icons {
  align-self: center;
}