.mdc-linear-progress__buffer {
  background-color: #513a8a5c !important;
}

.mdc-text-field__input {
  padding-right: 1em !important;
}

.mdc-text-field:not(.mdc-text-field--disabled) .mdc-text-field__input {
  color: #fafafa !important;
}

.mdc-text-field:not(.mdc-text-field--disabled) + .mdc-text-field-helper-line .mdc-text-field-helper-text {
  color: #fafafa !important;
}

.mdc-checkbox__native-control:enabled:not(:checked):not(:indeterminate) ~ .mdc-checkbox__background {
  border-color: #f5f5f5b8 !important;
}

.mdc-checkbox__checkmark {
  color: #fafafa !important;
}

.mdc-checkbox__mixedmark {
  border-color: #fafafa !important;
}

#dialogChecklist-content .mdc-list-item__text {
  color: black;
}

#dialogCheckList-content .mdc-checkbox__native-control:enabled:not(:checked):not(:indeterminate) ~ .mdc-checkbox__background {
  border-color: #6a50ad !important;
}

.mdc-button:not(:disabled) {
  background-color: #5b3ea2 !important;
}

.mdc-data-table {
  min-width: 30em;
  background-color: #513a8a5c !important;
  max-height: 25em;
  overflow-y: auto;
}

.mdc-data-table__content {
  background-color: #fafafa !important;
}

.mdc-switch.mdc-switch--checked .mdc-switch__thumb, .mdc-switch.mdc-switch--checked .mdc-switch__track {
  background-color: #fafafa !important;
}

.material-icons {
  margin-right: 0.2em;
  cursor: pointer;
}

.mdc-text-field--disabled .mdc-text-field__input {
  color: #dbdbdb;
}

.mdc-text-field--disabled .mdc-floating-label {
  color: #dbdbdb;
}